<template>
  <Card shadow>
    <template #title>
      <Avatar icon="md-locate" size="small" style="color: #2f54eb; background: #f0f5ff" />
      <span class="ivu-pl-8">前端日志</span>
    </template>
    <template #extra>
      <Tooltip transfer content="导出日志" placement="top">
        <Button type="dashed" icon="ios-cloud-download-outline" @click="exportLog" style="margin-right: 10px" />
      </Tooltip>
      <Tooltip transfer content="清空日志" placement="top">
        <Button type="dashed" icon="md-trash" @click="cleanLog" />
      </Tooltip>
    </template>
    <div :class="`${$config.cssPrefix}table-no-border`">
      <Table ref="table" :columns="columns" :data="tableData" @on-sort-change="changeSort">
        <template #type="{ row }">
          <Tag :color="row.type === `error` ? `red` : `blue`">{{ row.type }}</Tag>
        </template>
        <template #page="{ row }">{{ row.meta.url }}</template>
        <template #request="{ row }">{{ row.meta.apiURL }}</template>
        <template #more="{ row }">
          <Button size="small" ghost type="primary" @click="viewLog(row)">查看</Button>
        </template>
      </Table>
      <Page
        show-total
        show-sizer
        show-elevator
        class="ivu-text-center"
        :total="logList.length"
        @on-change="changePage"
        @on-page-size-change="changePageSize"
      />
    </div>
  </Card>
</template>

<script>
  import { mapMutations } from 'vuex'
  import { cleanLog } from '@/store/mutation-types'

  export default {
    name: 'SystemLog',
    data() {
      return {
        columns: [
          {
            title: '序号',
            align: 'center',
            width: 65,
            render: (h, { row, col, index }) => h('span', (this.currentPage - 1) * this.pageSize + index + 1)
          },
          {
            title: '时间',
            key: 'time',
            width: 180,
            sortable: true
          },
          {
            title: '类型',
            key: 'type',
            width: 100,
            slot: 'type'
          },
          {
            title: '信息',
            key: 'message'
          },
          // {
          //   title: '状态码',
          //   key: 'code',
          //   width: 100,
          //   render: (h, { row }) => {
          //     return (
          //       <span>{row.code === 0 ? '-' : row.code}</span>
          //     )
          //   }
          // },
          {
            title: '触发页面',
            slot: 'page'
          },
          {
            title: '请求地址',
            slot: 'request'
          },
          {
            title: '详细信息',
            align: 'center',
            width: 100,
            slot: 'more'
          }
        ],
        tableData: [],
        currentPage: 1,
        pageSize: 10,
        timeOrder: 'desc'
      }
    },
    computed: {
      logList() {
        return this.$store.state.log.logList
      }
    },
    watch: {
      logList() {
        this.changePage(this.currentPage)
      }
    },
    created() {
      this.changePage()
    },
    methods: {
      ...mapMutations([cleanLog]),
      exportLog() {
        if (!this.logList.length) {
          this.$Message.info('没有日志或异常')
          return
        }
        this.$refs.table.exportCsv({
          filename: '错误日志.csv'
        })
      },
      viewLog(log) {
        this.$Message.info('按F12打开控制台查看完整日志')
        this.$console.capsule(this.$config.titleSuffix, '完整日志内容')
        console.group('完整日志')
        console.log('message: ', log.message)
        console.log('source: ', log.source)
        console.log('time: ', log.time)
        console.log('type: ', log.type)
        console.log('meta: ', log.meta)
        console.groupEnd()
      },
      changePage(currentPage) {
        this.currentPage = currentPage || 1
        const startSize = (this.currentPage - 1) * this.pageSize
        const sortList = this.logList
          .map(item => ({ ...item, timestamp: new Date(item.time) }))
          .sort((a, b) => {
            if (this.timeOrder === 'asc') return a.timestamp - b.timestamp
            return b.timestamp - a.timestamp
          })
        this.tableData = sortList.slice(startSize, startSize + this.pageSize)
      },
      changePageSize(pageSize) {
        this.pageSize = pageSize
        this.changePage(this.currentPage)
      },
      changeSort(params) {
        this.timeOrder = params.order
        this.changePage(this.currentPage)
      }
    }
  }
</script>
